//      

const util = require('../util/util');
const ajax = require('../util/ajax');
const Evented = require('../util/evented');
const loadTileJSON = require('./load_tilejson');
const normalizeURL = require('../util/mapbox').normalizeTileURL;
const TileBounds = require('./tile_bounds');

                                     
                                          
                                 
                                                 
                               

class RasterTileSource extends Evented                   {
                   
               
                    
                    
                
                   
                     

                                              
                           
                                             
                       
                           
             
                         

                     
                                      

    constructor(id        , options                         , dispatcher            , eventedParent         ) {
        super();
        this.id = id;
        this.dispatcher = dispatcher;
        this.setEventedParent(eventedParent);

        this.type = 'raster';
        this.minzoom = 0;
        this.maxzoom = 22;
        this.roundZoom = true;
        this.scheme = 'xyz';
        this.tileSize = 512;
        this._loaded = false;

        this._options = util.extend({}, options);
        util.extend(this, util.pick(options, ['url', 'scheme', 'tileSize']));
    }

    load() {
        this.fire('dataloading', {dataType: 'source'});
        loadTileJSON(this._options, this.map._transformRequest, (err, tileJSON) => {
            if (err) {
            this.fire('error', err);
        } else if (tileJSON) {
            util.extend(this, tileJSON);
            if (tileJSON.bounds) this.tileBounds = new TileBounds(tileJSON.bounds, this.minzoom, this.maxzoom);

            // `content` is included here to prevent a race condition where `Style#_updateSources` is called
            // before the TileJSON arrives. this makes sure the tiles needed are loaded once TileJSON arrives
            // ref: https://github.com/mapbox/mapbox-gl-js/pull/4347#discussion_r104418088
            this.fire('data', {dataType: 'source', sourceDataType: 'metadata'});
            this.fire('data', {dataType: 'source', sourceDataType: 'content'});
        }
    });
}

onAdd(map     ) {
    this.map = map;
    this.load();
}

serialize() {
    return util.extend({}, this._options);
}

hasTile(coord           ) {
    return !this.tileBounds || this.tileBounds.contains(coord, this.maxzoom);
}

loadTile(tile      , callback                ) {
    //const url = normalizeURL(tile.coord.url(this.tiles, null, this.scheme), this.url, this.tileSize);

//
    let url = null;
    if(this._options.rasterType === 'hotarea'){
        let zoomOffset = parseInt(this._options.zoomOffset) ? parseInt(this._options.zoomOffset) : 0;
        url = normalizeURL(tile.coord.url(this.tiles, null, this.scheme, zoomOffset), this.url, this.tileSize);
        tile.request = ajax.getJSON(url, done_getJSON.bind(this));

        function done_getJSON(err, tileJSON) {
            delete tile.request;
            if (err) {
                //this.state = 'errored';
                return false;
                //return callback(err);
            }
            if(window.GeoGlobe && window.GeoGlobe.Layer && window.GeoGlobe.Layer.HotArea){
                window.GeoGlobe.Layer.HotArea["callback_tileJSON_"+this.id]({
                    data: tileJSON,
                    tileUrl: url,
                    layerid: this.id,
                    map: this.map
                });
                //tile.state = 'loaded';
                //callback(null);
            }
        }

    } else {
        if(this._options.rasterType === 'baidu'){
            url = normalizeURL(tile.coord.url4bd(this.tiles, null, this.scheme), this.url, this.tileSize);
        }else{
            let zoomOffset = parseInt(this._options.zoomOffset) ? parseInt(this._options.zoomOffset) : 0;
            url = normalizeURL(tile.coord.url(this.tiles, null, this.scheme, zoomOffset), this.url, this.tileSize);
        }
        tile.request = ajax.getImage(this.map._transformRequest(url, ajax.ResourceType.Tile), (err, img) => {
            delete tile.request;

        if (tile.aborted) {
            this.state = 'unloaded';
            callback(null);
        } else if (err) {
            this.state = 'errored';
            callback(err);
        } else if (img) {
            if (this.map._refreshExpiredTiles) tile.setExpiryData(img);
            delete (img     ).cacheControl;
            delete (img     ).expires;

            const gl = this.map.painter.gl;
            tile.texture = this.map.painter.getTileTexture(img.width);
            if (tile.texture) {
                gl.bindTexture(gl.TEXTURE_2D, tile.texture);
                gl.texSubImage2D(gl.TEXTURE_2D, 0, 0, 0, gl.RGBA, gl.UNSIGNED_BYTE, img);
            } else {
                tile.texture = gl.createTexture();
                gl.bindTexture(gl.TEXTURE_2D, tile.texture);
                gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR_MIPMAP_NEAREST);
                let filterVal = this.map.isIntScrollZoom===true ? gl.NEAREST : gl.LINEAR;//20170720
                gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, filterVal);
                gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
                gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
                gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, (true     ));
                //if (this.map.painter.extTextureFilterAnisotropic) {//
                //    gl.texParameterf(gl.TEXTURE_2D, this.map.painter.extTextureFilterAnisotropic.TEXTURE_MAX_ANISOTROPY_EXT, this.map.painter.extTextureFilterAnisotropicMax);
                //}

                gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, gl.UNSIGNED_BYTE, img);
                tile.texture.size = img.width;
            }
            gl.generateMipmap(gl.TEXTURE_2D);

            tile.state = 'loaded';

            callback(null);
        }
    });
}
}

abortTile(tile      ) {
    if (tile.request) {
        tile.request.abort();
        delete tile.request;
    }
}

unloadTile(tile      ) {
    if (tile.texture) this.map.painter.saveTileTexture(tile.texture);
}
}

module.exports = RasterTileSource;
