'use strict';

const util = require('../util/util');

const Map = require('./map');
const Transform = require('../geo/transform');
const WhooTS = require('@mapbox/whoots-js');

const defaultMinZoom = 0;
const defaultMaxZoom = 20;
const defaultOptions = {
    center: [0, 0],
    zoom: 0,
    bearing: 0,
    pitch: 0,

    minZoom: defaultMinZoom,
    maxZoom: defaultMaxZoom,

    interactive: true,

    scrollZoom: true,
    boxZoom: true,
    dragRotate: true,
    dragPan: true,
    keyboard: true,
    doubleClickZoom: true,
    touchZoomRotate: true,

    bearingSnap: 7,

    hash: false,

    attributionControl: true,

    failIfMajorPerformanceCaveat: false,
    preserveDrawingBuffer: false,

    trackResize: true,

    renderWorldCopies: true
};

class FreeCRSMap extends Map {

    constructor(options) {
        super(options);
    }

    initMapCRS(options) {
		this.transform.units = options.units;
        this._tileExtent = [-20037508.3427892, -20037508.3427892, 20037508.3427892, 20037508.3427892];
        if(options.mapCRS) {
            this._mapCRS = options.mapCRS;

            if(options.mapCRS.coordtransform) {
                if(options.mapCRS.coordtransform != "default") {
                    if(options.mapCRS.topTileExtent && Array.isArray(options.mapCRS.topTileExtent) && options.mapCRS.topTileExtent.length === 4) {
                        this._tileExtent[0] = options.mapCRS.topTileExtent[0];
                        this._tileExtent[1] = options.mapCRS.topTileExtent[1];
                        this._tileExtent[2] = options.mapCRS.topTileExtent[2];
                        this._tileExtent[3] = options.mapCRS.topTileExtent[3];
                    }

                    var tileWidth = this._tileExtent[2] - this._tileExtent[0];
                    var tileHeight = this._tileExtent[3] - this._tileExtent[1];

                    WhooTS.getTileBBox = function getTileBBox(x, y, z) {
                        var zoom = Math.pow(2, z);
                        y = (zoom - y - 1);
                        var minx = options.mapCRS.topTileExtent[0] + tileWidth * x / zoom;
                        var miny = options.mapCRS.topTileExtent[1] + tileHeight * y / zoom;
                        var maxx = options.mapCRS.topTileExtent[0] + tileWidth * (x + 1) / zoom;
                        var maxy = options.mapCRS.topTileExtent[1] + tileHeight * (y + 1) / zoom;

                        return minx + ',' + miny + ',' + maxx + ',' + maxy;
                    }

                    var needtrans = true;
                    if(options.mapCRS.coordtransform == "none") {
                        needtrans = false;
                    } else if(typeof options.mapCRS.coordtransform.toViewCoord != 'function') {
                        needtrans = false;
                    } else if(typeof options.mapCRS.coordtransform.fromViewCoord != 'function') {
                        needtrans = false;
                    }

                    if(needtrans) {
                        var left_bottom = options.mapCRS.coordtransform.toViewCoord(this._tileExtent[0], this._tileExtent[1]);
                        var right_top = options.mapCRS.coordtransform.toViewCoord(this._tileExtent[2], this._tileExtent[3]);
                        this.transform.latRange = [left_bottom[1], right_top[1]];
                        this.transform.lngRange = [left_bottom[0], right_top[0]];
                        var dataExtent = [this._tileExtent[0], this._tileExtent[1], this._tileExtent[2], this._tileExtent[3]];
                        var viewExtent = [left_bottom[0], left_bottom[1], right_top[0], right_top[1]];

                        this._transformCRS(dataExtent, viewExtent, options.style, options.mapCRS.coordtransform.toViewCoord, options.mapCRS.coordtransform.fromViewCoord);
                    } else {
                        this.transform.latRange = [this._tileExtent[1], this._tileExtent[3]];
                        this.transform.lngRange = [this._tileExtent[0], this._tileExtent[2]];
                        var tileExtent = [this._tileExtent[0], this._tileExtent[1], this._tileExtent[2], this._tileExtent[3]];

                        this._uniformCRS(tileExtent, options.style);
                    }
                }
            }
        }
    }

    _uniformCRS(tileExtent, style) {
        var extentWidth = tileExtent[2] - tileExtent[0];
        var extentHeight = tileExtent[3] - tileExtent[1];
        var centerx = (tileExtent[2] + tileExtent[0]) / 2;
        var centery = (tileExtent[3] + tileExtent[1]) / 2;

        Transform.prototype.lngX = function lngX (lng, worldSize) {
            return (lng - tileExtent[0]) * (worldSize || this.worldSize) / extentWidth;
        };
        Transform.prototype.latY = function latY (lat, worldSize) {
            return (tileExtent[3] - lat) * (worldSize || this.worldSize) / extentHeight;
        };
        Transform.prototype.xLng = function xLng (x, worldSize) {
            return x * extentWidth / (worldSize || this.worldSize) + tileExtent[0];
        };
        Transform.prototype.yLat = function yLat (y, worldSize) {
            return tileExtent[3] - y * extentHeight / (worldSize || this.worldSize);
        };

        var funcstr = "function customprojection(p) {var  x = ((p[0]- (" + centerx + ")) / " + extentWidth + " + 0.5),y = 0.5 - ((p[1]- (" + centery + ")) / " + extentHeight + ");y = y < 0 ? 0 : y > 1 ? 1 : y;return [x, y, 0];}";
        var blob = new Blob([funcstr]);
        var blobURL = window.URL.createObjectURL(blob);

        if(style && style.sources) {
            for(var idx in style.sources) {
                if(style.sources[idx] && style.sources[idx].type == "geojson") {
                    style.sources[idx].data.customprj = blobURL;
                }
            }
        }

        this._geojson_customprj = blobURL;
    }

    _transformCRS(dataExtent, viewExtent, style, toViewCoord, fromViewCoord) {
        var d_extentWidth = dataExtent[2] - dataExtent[0];
        var d_extentHeight = dataExtent[3] - dataExtent[1];
        var d_centerx = (dataExtent[2] + dataExtent[0]) / 2;
        var d_centery = (dataExtent[3] + dataExtent[1]) / 2;

        var v_extentWidth = viewExtent[2] - viewExtent[0];
        var v_extentHeight = viewExtent[3] - viewExtent[1];
        var v_centerx = (viewExtent[2] + viewExtent[0]) / 2;
        var v_centery = (viewExtent[3] + viewExtent[1]) / 2;

        Transform.prototype.lngX = function lngX (lng, worldSize) {
            var lnglat = fromViewCoord(lng, 0);
            return (lnglat[0] - dataExtent[0]) * (worldSize || this.worldSize) / d_extentWidth;
        };
        Transform.prototype.latY = function latY (lat, worldSize) {
            var lnglat = fromViewCoord(0, lat);
            return (dataExtent[3] - lnglat[1]) * (worldSize || this.worldSize) / d_extentHeight;
        };
        Transform.prototype.xLng = function xLng (x, worldSize) {
            var xx = x * d_extentWidth / (worldSize || this.worldSize) + dataExtent[0];
            var xy = toViewCoord(xx, 0);
            return xy[0];
        };
        Transform.prototype.yLat = function yLat (y, worldSize) {
            var yy = dataExtent[3] - y * d_extentHeight / (worldSize || this.worldSize);
            var xy = toViewCoord(0, yy);
            return xy[1];
        };

        var fromViewCoordStr = fromViewCoord.toString(0);
        fromViewCoordStr = "function fromViewCoord" + fromViewCoordStr.substring(8);
        var customprojectionStr = "function customprojection(p) {var pp = fromViewCoord(p[0],p[1]); var  x = ((pp[0]- (" + d_centerx + ")) / " + d_extentWidth + " + 0.5),y = 0.5 - ((pp[1]- (" + d_centery + ")) / " + d_extentHeight + ");y = y < 0 ? 0 : y > 1 ? 1 : y;return [x, y, 0];}";
        var funcstr = fromViewCoordStr + "  " + customprojectionStr;

        var blob = new Blob([funcstr]);
        var blobURL = window.URL.createObjectURL(blob);

        if(style && style.sources) {
            for(var idx in style.sources) {
                if(style.sources[idx] && style.sources[idx].type == "geojson") {
                    style.sources[idx].data.customprj = blobURL;
                }
            }
        }

        this._geojson_customprj = blobURL;
    }

    /**
     * addSource
     * mapCRSgeojsongeojsoncustomprj
     */
    addSource(id, source) {
        if(this._mapCRS && source && source.type == "geojson" && source.data && !source.data.customprj){
            source.data.customprj = this._geojson_customprj;
        }

        this.style.addSource(id, source);
        this._update(true);
        return this;
    }
}

module.exports = FreeCRSMap;
